.form-add {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 70%;
}

.input-form {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 60%;
  padding-bottom: 8%;
}

.font {
  font-size: 16px;
  text-align: center;
}

@media screen and (max-width: 540px) {
  .form-add {
    width: 100%;
  }
  .font {
    font-size: 16px;
    text-align: center;
  }
  .input-font {
    font-size: 16px;
  }
}

@media screen and (min-device-width: 540px) and (max-width: 1280px) {
  .form-add {
    width: 100%;
  }
}
